body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  box-sizing: border-box;
  font-family: monospace;
}

.container {
  padding: 1rem;
  display: block;
}

#drum-machine {
  display: grid;
  grid-template-columns: 120px 120px 120px;
  grid-template-rows: auto;
  border: 2px solid black;
  padding: 0.5rem;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  /* background: #000000; */
  background: url("https://i.postimg.cc/63CgTZBc/maxresdefault.jpg") #222222;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.drum-pad {
  font-size: 1.5rem;
  margin: 0.5rem;
  padding: 0.5rem;
  border: 2px solid #dddddd;
  background-color: rgba(0, 0, 0, 0.65);
  text-shadow: 0 0 10px #000000;
  color: #dddddd;
  overflow: hidden;
}

/* so all click events inside .drum-pad will happen on .drum-pad itself */
.drum-pad > * {
  pointer-events: none;
}

.sound-name {
  text-transform: capitalize;
  display: block;
  font-size: 0.75rem;
  /* display: none; */
}

.playing {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  border-color: gold;
  box-shadow: 0 0 2rem gold, 0 0 2rem inset;
  transition: all 100ms ease-in-out;
}

.enabled-switch {
  background: linear-gradient(to bottom, #94d581, #72c76d);
}

.disabled-switch {
  background: linear-gradient(to bottom, #3f3f3f, #000000);
  color: #eeeeee;
}

#pianoId {
  border-radius: 3px 0 0 3px;
  border: 2px solid black;
  border-right: 1px solid black;
}

#drumId {
  border-radius: 0 3px 3px 0;
  border: 2px solid black;
  border-left: 1px solid black;
}

.switcher {
  width: 100px;
  font-weight: bold;
  height: auto;
  padding: 0.25rem;
  margin: auto 0;
  cursor: pointer;
  transition: all 200ms;
}

#display {
  height: auto;
  width: 10rem;
  margin: 0.5rem auto;
  padding: 0.25rem;
  background: #000000;
  color: #bbbbbb;
  border: 2px solid #555555;
  text-transform: capitalize;
}

@media screen and (max-width: 700px) {
  #drum-machine {
    grid-template-columns: 110px 110px 110px;
  }
}

@media screen and (max-width: 500px) {
  #drum-machine {
    grid-template-columns: 100px 100px 100px;
  }

  .sound-name {
    font-size: 0.7rem;
  }
}

@media screen and (max-width: 400px) {
  #drum-machine {
    grid-template-columns: 85px 85px 85px;
    font-size: 0.85rem;
  }

  .sound-name {
    font-size: 0.65rem;
  }
}

/* iPad */
@media only screen and (min-width: 768px) and (max-width: 768px) and (min-height: 1024px) and (max-height: 1024px) {
  #heading {
    font-size: 2.5rem;
  }

  #drum-machine {
    grid-template-columns: 140px 140px 140px;
    grid-template-rows: 110px 110px 110px;
  }

  .drum-pad {
    font-size: 1.5rem;
  }

  .sound-name,
  #display {
    margin-top: 0.5rem;
    font-size: 1.25rem;
  }

  #switch-heading {
    font-size: 1.5rem;
    margin: 1rem auto !important;
  }

  #switch-instrument {
    font-size: 1.2rem;
  }
}

